/* ハンバーガーメニュー */

.c-hamburger {
  position: fixed;
  width: 50px;
  height: 50px;
  display: block;
  border: none;
  background: transparent;
  cursor: pointer;
  margin-left: 5%;
  margin-right: 0%;
  margin-top: 60px;
  z-index: 1;
}

.c-hamburger span {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 2px;
  background: black;
  transition: all 0.3s;
}

.c-hamburger span:first-child {
  top: 0;
}

.c-hamburger span:nth-child(2) {
  /* top: 50%; */
  transform: translateY(-50%);
}

.c-hamburger span:nth-child(2)::after {
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  width: 100%;
  height: 2px;
  background-color: black;
  transition: all 0.3s;
}

.c-hamburger span:last-child {
  bottom: 0;
}

/* ハンバーガーメニューが開いたら */
.c-hamburger.is-open span:first-child {
  transform: translateY(20px) scale(0);
}

.c-hamburger.is-open span:nth-child(2) {
  transform: rotate(-45deg);
}

.c-hamburger.is-open span:nth-child(2)::after {
  transform: rotate(90deg);
}

.c-hamburger.is-open span:last-child {
  transform: translateY(-20px) scale(0);
}

.nav-hamburger {
  position: fixed;
  top: 150px;
  margin-left: 5%;
  width: 200px;
  opacity: 0;
  visibility: hidden;
  transition: opacity .5s, visibility .5s;
  z-index: 2;
  font-family: 'Avenir Next',YuGothic,'Yu Gothic',sans-serif;
  font-weight: bold;
}

.nav-hamburger.show {
  position: fixed;
  top: 150px;
  margin-left: 5%;
  width: 200px;
  opacity: 1;
  visibility: visible;
  font-family: 'Avenir Next',YuGothic,'Yu Gothic',sans-serif;
  font-weight: bold;
}

li.nav-hamburger_menu_li {
  font-size: min(28px, 2vw);
  line-height: 200%;
  margin-left: 5%;
  color: rgb(160, 160, 160);
}


